.rootTransition-appear {
  opacity: 0; }

.rootTransition-appear.rootTransition-appear-active {
  opacity: 1;
  -webkit-transition: all 2s ease-out;
  -o-transition: all 2s ease-out;
  transition: all 2s ease-out; }

.hobbies {
  background: url(/images/background/hobbies.png) no-repeat center center fixed !important;
  margin-top: -105px;
  padding-top: 50px;
  padding-bottom: 200px; }
  .hobbies .hobby-list {
    margin: 0px; }
    .hobbies .hobby-list ul {
      list-style-type: none; }
    .hobbies .hobby-list h3 {
      font: bold 16px/1.5 Helvetica, Verdana, sans-serif; }
    .hobbies .hobby-list li {
      padding: 10px; }
      .hobbies .hobby-list li a {
        text-decoration: none; }
      .hobbies .hobby-list li img {
        float: left;
        width: 20%;
        margin: 0 10px 0 0; }



